function changeToSearchPosition(jqEvent) {
  console.log(jqEvent);
}

document.addEventListener("DOMContentLoaded", () => {
  const jobtableAppMain = document.querySelector("#jobtable-app");
  const filterBtn = jobtableAppMain.querySelector("[data-filter]");

  $("[data-close-popup]").on("click", (jqEvent) => {
    $(jqEvent.currentTarget).parents("[data-popup]").removeClass("open");
  });

  $("[data-filter]")
    .off("click")
    .on("click", (jqEvent) => {
      //
      $("[data-type='filter']").addClass("open");
      //
    });

  $("[data-settings]")
    .off("click")
    .on("click", (jqEvent) => {
      //
      $("[data-type='settings']").addClass("open");
      //
    });

  $("[data-add-group]")
    .off("click")
    .on("click", (jqEvent) => {
      jqEvent.preventDefault();
      //
      $("[data-type='add-group']").addClass("open");
      //
    });
  $("[data-add-position]")
    .off("click")
    .on("click", (jqEvent) => {
      jqEvent.preventDefault();
      //
      $("[data-type='add-position']").addClass("open");
      //
    });
  $("#search-position")
    .off("select2:select")
    .on("select2:select", changeToSearchPosition);
});
